'use client';

import React, { useEffect, useState } from 'react';
import { useParams, useRouter, useSearchParams } from 'next/navigation';
import { motion } from 'framer-motion';
import { Clock, ArrowLeft, Tag, FolderOpen, Share2, Download, ExternalLink } from 'lucide-react';
import api, { getSettings, getPageText } from '@/lib/api';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';
import { getApiBaseUrl } from '@/lib/utils';
import NoteShareCard from '@/components/NoteShareCard';
import DiskModal from '@/components/DiskModal';
import Copyright from '@/components/Copyright';
import CopyButton from '@/components/CopyButton';
import SEOMeta from '@/components/SEOMeta';
import PageBackground from '@/components/PageBackground';

const API_BASE_URL = getApiBaseUrl();

interface Note {
  id: number;
  title: string;
  content: string;
  is_published: boolean;
  is_pinned?: boolean;
  published_at?: string;
  created_at: string;
  media_type?: 'none' | 'image' | 'video' | 'music';
  media_urls?: string[];
  external_link?: string;
  category?: string;
  category_id?: number;
  tags?: string;
  password?: string;
  requiresPassword?: boolean;
  Tags?: Array<{
    id: number;
    name: string;
    color?: string;
  }>;
  source_type?: 'none' | 'original' | 'reprint';
  source_url?: string;
  source_text?: string;
}

const formatDateTime = (dateString: string) => {
  const date = new Date(dateString);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');

  return `${year}-${month}-${day} ${hours}:${minutes}`;
};

export default function NotePage() {
  const params = useParams();
  const router = useRouter();
  const searchParams = useSearchParams();
  const [note, setNote] = useState<Note | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [password, setPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [verifying, setVerifying] = useState(false);
  const [errorCount, setErrorCount] = useState(0);
  const [remaining, setRemaining] = useState(3);
  const [showShareCard, setShowShareCard] = useState(false);
  const [showDiskModal, setShowDiskModal] = useState(false);
  const [hasDisks, setHasDisks] = useState(false);
  const [verifiedPassword, setVerifiedPassword] = useState<string>('');
  // 页面文本配置和网站标题
  const [pageTextConfig, setPageTextConfig] = useState({
    browserTitle: '笔记详情',
    browserSubtitle: ''
  });
  const [siteTitle, setSiteTitle] = useState('');

  useEffect(() => {
    const fetchNote = async () => {
      try {
        const urlPassword = searchParams.get('password');

        if (urlPassword) {
          const response = await api.get(`/notes/${params.id}?password=${encodeURIComponent(urlPassword)}`);
          if (response.data?.success && response.data?.data) {
            const noteData = response.data.data;
            setNote(noteData);

            if (!noteData.requiresPassword) {
              setShowPasswordModal(false);
              setVerifiedPassword(urlPassword);
            } else {
              setShowPasswordModal(true);
              setPasswordError('URL中的密码不正确，请重新输入');
            }
          } else {
            setError(true);
          }
        } else {
          const response = await api.get(`/notes/${params.id}`);
          if (response.data?.success && response.data?.data) {
            const noteData = response.data.data;
            setNote(noteData);

            if (noteData.requiresPassword) {
              setShowPasswordModal(true);
            }
          } else {
            setError(true);
          }
        }
      } catch (error) {
        console.error('Failed to fetch note:', error);
        setError(true);
      } finally {
        setLoading(false);
      }
    };

    if (params.id) {
      fetchNote();
    }
  }, [params.id, searchParams]);

  // 加载页面文本配置和网站标题
  useEffect(() => {
    const loadPageConfig = async () => {
      try {
        const [settingsResponse, pageTextResponse] = await Promise.all([
          getSettings(),
          getPageText('note')
        ]);

        if (settingsResponse.success && settingsResponse.data) {
          const settingsObj: Record<string, string> = {};
          settingsResponse.data.forEach((setting: { key: string; value: string }) => {
            settingsObj[setting.key] = setting.value;
          });
          setSiteTitle(settingsObj.siteTitle || '');
        }

        if (pageTextResponse.success && pageTextResponse.data) {
          setPageTextConfig({
            browserTitle: pageTextResponse.data.browserTitle || '笔记详情',
            browserSubtitle: pageTextResponse.data.browserSubtitle || ''
          });
        }
      } catch (err) {
        console.error('加载页面配置失败:', err);
      }
    };
    loadPageConfig();
  }, []);

  // 设置页面标题：[文章名称] - [笔记页面标题] - [网站标题]
  useEffect(() => {
    if (note && !note.requiresPassword) {
      const noteTitle = note.title || '无标题';
      const parts = [noteTitle];
      if (pageTextConfig.browserTitle) {
        parts.push(pageTextConfig.browserTitle);
      }
      if (siteTitle) {
        parts.push(siteTitle);
      }
      document.title = parts.join(' - ');
    } else if (loading) {
      document.title = '加载中...';
    } else if (error) {
      document.title = '笔记不存在';
    }
  }, [note, loading, error, verifiedPassword, pageTextConfig, siteTitle]);

  useEffect(() => {
    if (note && !note.requiresPassword) {
      checkNoteDisks();
    }
  }, [note, verifiedPassword]);

  // 获取密码尝试状态
  useEffect(() => {
    const fetchPasswordStatus = async () => {
      try {
        const response = await api.get('/notes/password-status');
        if (response.data?.success) {
          setErrorCount(response.data.errorCount || 0);
          setRemaining(response.data.remaining ?? 3);
        }
      } catch (error) {
        console.error('Failed to fetch password status');
      }
    };
    if (showPasswordModal) {
      fetchPasswordStatus();
    }
  }, [showPasswordModal]);

  const checkNoteDisks = async () => {
    try {
      const passwordParam = verifiedPassword ? `?password=${encodeURIComponent(verifiedPassword)}` : '';
      const response = await api.get(`/notes/${params.id}/disks${passwordParam}`);
      if (response.data?.success && response.data?.data) {
        setHasDisks(response.data.data.length > 0);
      }
    } catch (error) {
      setHasDisks(false);
    }
  };

  const handlePasswordSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setPasswordError('');
    setVerifying(true);

    try {
      const response = await api.post(`/notes/${params.id}/verify`, { password });
      if (response.data?.success && response.data?.data) {
        setNote(response.data.data);
        setShowPasswordModal(false);
        setVerifiedPassword(password);
        setPassword('');
        setErrorCount(0);
        setRemaining(3);
      }
    } catch (error: any) {
      const data = error.response?.data;
      if (data?.errorCount !== undefined) {
        setErrorCount(data.errorCount);
        setRemaining(data.remaining);
      }
      if (error.response?.status === 429) {
        setPasswordError('密码错误次数过多，请1小时后重试！');
      } else if (error.response?.status === 401) {
        setPasswordError(data?.remaining > 0 ? '密码错误' : '密码错误次数过多，请1小时后重试！');
      } else {
        setPasswordError('验证失败，请稍后重试');
      }
    } finally {
      setVerifying(false);
    }
  };

  const handleBack = () => {
    router.push('/');
  };

  const handleCategoryClick = () => {
    if (note?.category) {
      router.push(`/notes?category=${encodeURIComponent(note.category)}`);
    }
  };

  const handleTagClick = (tagName: string) => {
    router.push(`/notes?tag=${encodeURIComponent(tagName)}`);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-bg-primary flex items-center justify-center">
        <div className="text-center">
          <div className="w-10 h-10 border-3 border-primary-500 border-t-transparent rounded-full animate-spin mx-auto mb-3"></div>
          <p className="text-text-tertiary text-sm">加载中...</p>
        </div>
      </div>
    );
  }

  if (error || !note) {
    return (
      <div className="min-h-screen bg-bg-primary flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-3xl font-bold text-text-primary mb-3">404</h1>
          <p className="text-text-tertiary text-sm mb-4">笔记不存在或已被删除</p>
          <button
            onClick={handleBack}
            className="px-4 py-2 bg-primary-500 text-white text-sm rounded-lg hover:bg-primary-600 transition-colors"
          >
            返回首页
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-bg-primary hide-scrollbar relative">
      <SEOMeta />
      {/* 背景图片 */}
      <PageBackground />
      {/* 顶部导航栏 - 固定在顶部 */}
      <nav className="sticky top-0 z-50 bg-bg-primary/95 backdrop-blur-md border-b border-border-primary shadow-sm">
        <div className="max-w-6xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            {/* 左侧：返回按钮 */}
            <button
              onClick={handleBack}
              className="flex items-center gap-2 text-text-secondary hover:text-text-primary transition-colors text-sm font-medium"
            >
              <ArrowLeft className="w-5 h-5" />
              <span>返回</span>
            </button>

            {/* 右侧：操作按钮 */}
            <div className="flex items-center gap-3">
              {hasDisks && (
                <button
                  onClick={() => setShowDiskModal(true)}
                  className="flex items-center gap-1.5 px-3 py-2 text-sm text-primary-600 hover:bg-primary-50 rounded-lg transition-colors font-medium"
                >
                  <Download className="w-4 h-4" />
                  <span>下载资源</span>
                </button>
              )}
              <button
                onClick={() => setShowShareCard(true)}
                className="flex items-center gap-1.5 px-3 py-2 text-sm text-primary-600 hover:bg-primary-50 rounded-lg transition-colors font-medium"
              >
                <Share2 className="w-4 h-4" />
                <span>分享笔记</span>
              </button>
            </div>
          </div>
        </div>
      </nav>

      {/* 主内容区 */}
      <main className="py-6">
        <div className="max-w-6xl mx-auto px-4">
          {/* 文章内容 */}
          <motion.article
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3 }}
            className="rounded-xl border border-border-primary shadow-sm overflow-hidden bg-bg-secondary"
          >
            {/* 文章头部 */}
            <header className="bg-bg-tertiary px-4 sm:px-6 py-4 border-b border-border-primary">
              {/* 标题行 */}
              <div className="flex flex-wrap items-start gap-2 mb-3">
                <h1 className="text-lg sm:text-xl font-bold text-text-primary leading-snug flex-1">
                  {note.title || '无标题'}
                </h1>

                {/* 标签徽章 */}
                <div className="flex flex-wrap gap-1.5">
                  {note.source_type === 'original' && (
                    <span className="inline-flex items-center px-2 py-0.5 bg-primary-100 text-primary-700 text-[10px] font-medium rounded">
                      原创
                    </span>
                  )}
                  {note.source_type === 'reprint' && (
                    <span className="inline-flex items-center px-2 py-0.5 bg-primary-100 text-primary-600 text-[10px] font-medium rounded">
                      转载
                    </span>
                  )}
                  {hasDisks && (
                    <span className="inline-flex items-center px-2 py-0.5 bg-primary-100 text-primary-600 text-[10px] font-medium rounded">
                      含资源
                    </span>
                  )}
                </div>
              </div>

              {/* 元信息 */}
              <div className="flex flex-wrap items-center gap-x-3 gap-y-1.5 text-xs text-text-tertiary">
                {/* 分类 */}
                {note.category && (
                  <button
                    onClick={handleCategoryClick}
                    className="flex items-center gap-1 hover:text-primary-600 transition-colors"
                  >
                    <FolderOpen className="w-3 h-3" />
                    <span>{note.category}</span>
                  </button>
                )}

                {/* 标签 */}
                {note.Tags && note.Tags.length > 0 && note.Tags.map((tag) => (
                  <button
                    key={tag.id}
                    onClick={() => handleTagClick(tag.name)}
                    className="flex items-center gap-1 hover:opacity-70 transition-opacity"
                    style={{ color: tag.color || 'var(--text-tertiary)' }}
                  >
                    <Tag className="w-3 h-3" />
                    <span>{tag.name}</span>
                  </button>
                ))}

                {/* 旧格式标签 */}
                {(!note.Tags || note.Tags.length === 0) && note.tags && note.tags.split(',').map((tag, index) => (
                  <button
                    key={index}
                    onClick={() => handleTagClick(tag.trim())}
                    className="flex items-center gap-1 hover:text-primary-600 transition-colors"
                  >
                    <Tag className="w-3 h-3" />
                    <span>{tag.trim()}</span>
                  </button>
                ))}

                {/* 时间 */}
                <div className="flex items-center gap-1">
                  <Clock className="w-3 h-3" />
                  <time dateTime={note.published_at || note.created_at}>
                    {formatDateTime(note.published_at || note.created_at)}
                  </time>
                </div>
              </div>
            </header>

            {/* 文章内容区 */}
            <div className="px-4 sm:px-6 py-5">
              <div className="prose prose-sm max-w-none note-content">
                <ReactMarkdown
                  remarkPlugins={[remarkGfm]}
                  rehypePlugins={[rehypeRaw]}
                  components={{
                    img: ({ src, alt, ...props }) => {
                      let imageSrc = src;
                      if (typeof src === 'string' && src.startsWith('/uploads/')) {
                        imageSrc = `${API_BASE_URL}${src}`;
                      }
                      return (
                        <div className="flex justify-center my-4">
                          <img
                            src={imageSrc}
                            alt={alt || ''}
                            className="max-w-full h-auto rounded-lg shadow-sm"
                            style={{ maxHeight: '360px', objectFit: 'contain' }}
                            loading="lazy"
                            onError={(e) => {
                              (e.target as HTMLImageElement).style.display = 'none';
                            }}
                            {...props}
                          />
                        </div>
                      );
                    },
                    iframe: ({ node, ...props }) => (
                      <div className="iframe-container my-4">
                        <iframe {...props} />
                      </div>
                    ),
                    pre: ({ node, children, ...props }: any) => {
                      // 提取代码文本用于复制
                      const getCodeText = (children: React.ReactNode): string => {
                        if (typeof children === 'string') return children;
                        if (Array.isArray(children)) {
                          return children.map(getCodeText).join('');
                        }
                        if (React.isValidElement(children)) {
                          const childProps = children.props as { children?: React.ReactNode };
                          return getCodeText(childProps.children || '');
                        }
                        return '';
                      };
                      const codeText = getCodeText(children);

                      // 提取语言类型
                      const getLanguage = (children: React.ReactNode): string => {
                        if (React.isValidElement(children)) {
                          const childProps = children.props as { className?: string };
                          const className = childProps?.className || '';
                          const match = /language-(\w+)/.exec(className);
                          return match ? match[1] : '';
                        }
                        return '';
                      };
                      const language = getLanguage(children);

                      return (
                        <div className="relative my-4 group code-block-wrapper">
                          <pre className="code-block" {...props}>
                            {language && (
                              <span className="code-language-label">{language}</span>
                            )}
                            {children}
                          </pre>
                          <CopyButton text={codeText} />
                        </div>
                      );
                    },
                    code: ({ node, inline, className, children, ...props }: any) => {
                      if (inline) {
                        return (
                          <code className="inline-code" {...props}>
                            {children}
                          </code>
                        );
                      }
                      return (
                        <code className={`block-code ${className || ''}`} {...props}>
                          {children}
                        </code>
                      );
                    },
                    table: ({ node, children, ...props }: any) => (
                      <div className="overflow-x-auto my-4 rounded-lg border border-border-primary">
                        <table className="w-full text-sm" {...props}>
                          {children}
                        </table>
                      </div>
                    ),
                    th: ({ node, children, ...props }: any) => (
                      <th className="bg-bg-tertiary text-text-primary font-medium p-2 text-left text-xs border-b border-border-primary" {...props}>
                        {children}
                      </th>
                    ),
                    td: ({ node, children, ...props }: any) => (
                      <td className="p-2 text-text-secondary text-xs border-b border-border-primary" {...props}>
                        {children}
                      </td>
                    ),
                    blockquote: ({ node, children, ...props }: any) => (
                      <blockquote className="note-blockquote" {...props}>
                        {children}
                      </blockquote>
                    ),
                    a: ({ node, href, children, ...props }: any) => (
                      <a
                        href={href}
                        className="text-primary-600 hover:text-primary-700 hover:underline"
                        target="_blank"
                        rel="noopener noreferrer"
                        {...props}
                      >
                        {children}
                      </a>
                    ),
                    p: ({ node, children, ...props }: any) => {
                      const hasTextNodes = React.Children.toArray(children).some(child =>
                        typeof child === 'string' && child.includes('\n')
                      );

                      if (hasTextNodes) {
                        return (
                          <p className="text-text-secondary leading-relaxed mb-3 text-sm" {...props}>
                            {React.Children.map(children, (child, index) => {
                              if (typeof child === 'string') {
                                const lines = child.split('\n');
                                return (
                                  <React.Fragment key={index}>
                                    {lines.map((line, lineIndex) => (
                                      <React.Fragment key={lineIndex}>
                                        {line}
                                        {lineIndex < lines.length - 1 && <br />}
                                      </React.Fragment>
                                    ))}
                                  </React.Fragment>
                                );
                              }
                              return child;
                            })}
                          </p>
                        );
                      }

                      return (
                        <p className="text-text-secondary leading-relaxed mb-3 text-sm" {...props}>
                          {children}
                        </p>
                      );
                    },
                    h1: ({ node, children, ...props }: any) => (
                      <h1 className="text-lg font-bold text-text-primary mb-3 mt-5 pb-2 border-b border-border-primary" {...props}>
                        {children}
                      </h1>
                    ),
                    h2: ({ node, children, ...props }: any) => (
                      <h2 className="text-base font-semibold text-text-primary mb-2 mt-4" {...props}>
                        {children}
                      </h2>
                    ),
                    h3: ({ node, children, ...props }: any) => (
                      <h3 className="text-sm font-semibold text-text-primary mb-2 mt-3" {...props}>
                        {children}
                      </h3>
                    ),
                    ul: ({ node, children, ...props }: any) => (
                      <ul className="list-disc pl-5 mb-3 space-y-1" {...props}>
                        {children}
                      </ul>
                    ),
                    ol: ({ node, children, ...props }: any) => (
                      <ol className="list-decimal pl-5 mb-3 space-y-1" {...props}>
                        {children}
                      </ol>
                    ),
                    li: ({ node, children, ...props }: any) => (
                      <li className="text-text-secondary text-sm" {...props}>
                        {children}
                      </li>
                    ),
                    hr: ({ node, ...props }: any) => (
                      <hr className="my-6 border-t border-border-primary" {...props} />
                    ),
                    strong: ({ node, children, ...props }: any) => (
                      <strong className="font-semibold text-text-primary" {...props}>
                        {children}
                      </strong>
                    ),
                  }}
                >
                  {note.content}
                </ReactMarkdown>
              </div>
            </div>

            {/* 底部信息区 */}
            <footer className="bg-bg-tertiary px-4 sm:px-6 py-4 border-t border-border-primary">
              {/* 文章来源 */}
              {note.source_type === 'reprint' && note.source_text && (
                <div className="flex items-start gap-2 text-xs text-text-tertiary mb-3 pb-3 border-b border-border-primary">
                  <ExternalLink className="w-3.5 h-3.5 mt-0.5 flex-shrink-0" />
                  <div>
                    <span className="text-text-tertiary">来源：</span>
                    <span>{note.source_text}</span>
                  </div>
                </div>
              )}

              {/* 操作按钮组 */}
              <div className="flex items-center justify-between">
                <button
                  onClick={handleBack}
                  className="flex items-center gap-1.5 text-xs text-text-tertiary hover:text-text-secondary transition-colors"
                >
                  <ArrowLeft className="w-3.5 h-3.5" />
                  <span>返回列表</span>
                </button>

                <div className="flex items-center gap-2">
                  {hasDisks && (
                    <button
                      onClick={() => setShowDiskModal(true)}
                      className="flex items-center gap-1.5 px-3 py-1.5 text-xs bg-primary-100 text-primary-600 rounded-md hover:bg-primary-200 transition-colors"
                    >
                      <Download className="w-3.5 h-3.5" />
                      <span>下载资源</span>
                    </button>
                  )}
                  <button
                    onClick={() => setShowShareCard(true)}
                    className="flex items-center gap-1.5 px-3 py-1.5 text-xs bg-primary-100 text-primary-600 rounded-md hover:bg-primary-200 transition-colors"
                  >
                    <Share2 className="w-3.5 h-3.5" />
                    <span>分享笔记</span>
                  </button>
                </div>
              </div>
            </footer>
          </motion.article>

          {/* 页脚版权 */}
          <footer className="mt-6 border-t border-border-primary py-6 text-center text-sm text-text-tertiary">
            <p>© {new Date().getFullYear()} Xs-blog. All rights reserved.</p>
            <Copyright />
          </footer>
        </div>
      </main>

      {/* 密码验证弹窗 */}
      {showPasswordModal && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-[9999] p-4">
          <motion.div
            className="bg-bg-secondary rounded-xl max-w-sm w-full p-5 shadow-xl border border-border-primary"
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.2 }}
          >
            <div className="text-center mb-5">
              <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-3">
                <svg className="w-6 h-6 text-primary-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                </svg>
              </div>
              <h3 className="text-base font-bold text-text-primary mb-1">需要密码访问</h3>
              <p className="text-xs text-text-tertiary">请输入密码查看笔记内容</p>
            </div>

            <form onSubmit={handlePasswordSubmit} className="space-y-3">
              <div>
                <input
                  type="password"
                  name="note-access-password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="请输入密码"
                  className="w-full px-3 py-2.5 text-sm border border-border-primary rounded-lg bg-bg-tertiary text-text-primary focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                  autoFocus
                  autoComplete="off"
                  disabled={verifying}
                />
                {passwordError && (
                  <p className="mt-1.5 text-xs" style={{ color: 'var(--error-text)' }}>{passwordError}</p>
                )}
                {errorCount > 0 && remaining > 0 && (
                  <p className="mt-1.5 text-xs text-text-tertiary">当前已输入错误{errorCount}次，还可以重试{remaining}次</p>
                )}
              </div>

              <div className="flex gap-2">
                <button
                  type="button"
                  onClick={handleBack}
                  className="flex-1 px-3 py-2 text-sm bg-bg-tertiary text-text-secondary rounded-lg hover:bg-border-primary transition-colors"
                  disabled={verifying}
                >
                  返回
                </button>
                <button
                  type="submit"
                  className={`flex-1 px-3 py-2 text-sm rounded-lg transition-colors ${remaining === 0 ? 'bg-gray-400 cursor-not-allowed' : 'bg-primary-500 hover:bg-primary-600 disabled:opacity-50'} text-white`}
                  disabled={verifying || !password || remaining === 0}
                >
                  {verifying ? '验证中...' : remaining === 0 ? '暂不可用' : '确认'}
                </button>
              </div>
            </form>
          </motion.div>
        </div>
      )}

      {/* 分享卡片 */}
      {showShareCard && note && (
        <NoteShareCard
          noteId={note.id}
          title={note.title}
          password={note.password}
          onClose={() => setShowShareCard(false)}
        />
      )}

      {/* 网盘弹窗 */}
      {note && (
        <DiskModal
          isOpen={showDiskModal}
          onClose={() => setShowDiskModal(false)}
          noteId={note.id}
          isAdmin={false}
          password={verifiedPassword}
        />
      )}
    </div>
  );
}
